import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import Navigation from "@/components/navigation"
import { ArrowRight, Network, Zap, BarChart3 } from "lucide-react"

export default function Home() {
  return (
    <div className="min-h-screen">
      <Navigation />

      {/* Hero Section */}
      <section className="relative px-4 py-32 sm:px-6 lg:px-8 overflow-hidden">
        <div className="relative max-w-6xl mx-auto">
          <div className="text-center">
            <div className="inline-flex items-center gap-2 px-4 py-2 rounded-full glass-card accent-border text-[10px] uppercase font-bold tracking-[0.3em] text-theme-muted animate-fade-in">
              <span className="w-1.5 h-1.5 rounded-full bg-theme-primary opacity-80 shadow-[0_0_10px_rgb(var(--theme-primary))]" />
              Next-Gen Oncology Intelligence
            </div>

            <h1 className="text-6xl md:text-8xl font-black text-white tracking-tighter leading-none max-w-4xl animate-slide-up">
              Decoding <span className="bg-gradient-to-r from-white via-[rgb(var(--theme-primary))] to-[rgb(var(--theme-secondary))] bg-clip-text text-transparent">Genetic</span> Complexity
            </h1>

            <p className="text-xl text-theme-muted max-w-2xl font-light leading-relaxed animate-slide-up" style={{ animationDelay: '0.2s' }}>
              Harnessing Spectral Graph Theory and Graph Attention Networks to identify novel cancer driver genes and predict patient survival with unprecedented precision.
            </p>

            <div
              className="flex flex-col sm:flex-row gap-4 justify-center mb-28 animate-slide-up"
              style={{ animationDelay: "0.3s" }}
            >
              <Link href="/upload">
                <Button
                  size="lg"
                  className="gap-2 bg-yellow-500 hover:bg-yellow-400 text-black shadow-[0_0_30px_rgba(234,179,8,0.3)] hover:shadow-[0_0_40px_rgba(234,179,8,0.5)] transition-all duration-500 text-base font-bold rounded-xl px-8"
                >
                  Start Analysis <ArrowRight className="w-4 h-4" />
                </Button>
              </Link>
              <Link href="/methodology">
                <Button
                  size="lg"
                  variant="outline"
                  className="shadow-md hover:shadow-lg transition-all duration-500 text-base font-semibold bg-white/5 border-white/20 text-white hover:bg-white/10 hover:border-yellow-400/50 rounded-xl px-8"
                >
                  Learn Our Approach
                </Button>
              </Link>
            </div>

            <div className="grid grid-cols-2 gap-4">
              {[
                { label: 'Gene Accuracy', value: '70.6%', color: 'var(--theme-primary)' },
                { label: 'Survival C-Index', value: '0.742', color: 'var(--theme-secondary)' },
              ].map((stat, i) => (
                <div key={i} className="p-4 rounded-2xl bg-white/5 border border-white/5">
                  <p className="text-[10px] text-white/30 uppercase font-bold mb-1">{stat.label}</p>
                  <p className="text-2xl font-black text-white" style={{ color: `rgb(${stat.color})` }}>{stat.value}</p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </section>

      {/* Key Features Section */}
      <section className="px-4 py-24 sm:px-6 lg:px-8 border-y border-white/5 bg-black/20 backdrop-blur-sm">
        <div className="max-w-6xl mx-auto">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold mb-4 animate-slide-up">Comprehensive Research Platform</h2>
            <p className="text-muted-foreground text-lg animate-slide-up" style={{ animationDelay: "0.1s" }}>
              Everything you need for advanced biological network analysis
            </p>
          </div>

          <div className="grid md:grid-cols-2 gap-8">
            {[
              {
                icon: Network,
                title: "PPI Network Integration",
                desc: "Upload protein-protein interaction data from multiple databases (STRING, HI-III, BioGRID)",
              },
              {
                icon: BarChart3,
                title: "Spectral Analysis",
                desc: "Compute spectral centrality, detect modules, and identify network bottlenecks",
              },
              {
                icon: Zap,
                title: "GNN Predictions",
                desc: "Graph attention networks for cancer driver gene prediction with high recall",
              },
              {
                icon: Network,
                title: "Enrichment Analysis",
                desc: "GO and KEGG pathway enrichment with functional validation",
              },
              {
                icon: BarChart3,
                title: "Survival Prediction",
                desc: "Patient risk stratification with C-index metrics on TCGA data",
              },
              {
                icon: Zap,
                title: "Clinical Dashboards",
                desc: "Interactive visualizations and actionable clinical recommendations",
              },
            ].map((feature, idx) => (
              <div
                key={idx}
                className="flex gap-4 p-8 rounded-2xl border border-white/5 bg-white/5 hover:bg-white/10 hover:border-yellow-400/30 hover:shadow-[0_0_30px_rgba(234,179,8,0.05)] transition-all duration-500 animate-slide-up group"
                style={{ animationDelay: `${idx * 0.05}s` }}
              >
                <div className="flex-shrink-0">
                  <div className="flex items-center justify-center h-14 w-14 rounded-xl bg-white/5 group-hover:bg-yellow-500/20 transition-all duration-500 border border-white/10">
                    <feature.icon className="h-7 w-7 text-yellow-500" />
                  </div>
                </div>
                <div className="text-left">
                  <h3 className="font-bold text-white text-xl mb-2 tracking-tight">{feature.title}</h3>
                  <p className="text-white/40 text-sm leading-relaxed font-light">{feature.desc}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="px-4 py-32 sm:px-6 lg:px-8 bg-gradient-to-b from-transparent to-yellow-500/5 backdrop-blur-sm border-t border-white/5">
        <div className="max-w-4xl mx-auto text-center">
          <h2 className="text-5xl font-extrabold text-white mb-6 animate-slide-up tracking-tight">Ready to Analyze Cancer Networks?</h2>
          <p className="text-xl text-white/50 mb-12 animate-slide-up font-light" style={{ animationDelay: "0.1s" }}>
            Upload your PPI data and start discovering cancer driver genes with BE Spectra's AI-powered analysis
            platform.
          </p>
          <Link href="/upload" className="inline-block animate-slide-up" style={{ animationDelay: "0.2s" }}>
            <Button
              size="lg"
              className="gap-3 bg-yellow-500 hover:bg-yellow-400 text-black shadow-[0_0_50px_rgba(234,179,8,0.2)] hover:shadow-[0_0_60px_rgba(234,179,8,0.4)] transition-all duration-500 text-lg font-bold rounded-xl px-12 py-7"
            >
              Begin Analysis <ArrowRight className="w-5 h-5" />
            </Button>
          </Link>
        </div>
      </section>

      {/* Footer */}
      <footer className="border-t border-white/5 px-4 py-20 sm:px-6 lg:px-8 bg-black/40 backdrop-blur-md">
        <div className="max-w-6xl mx-auto">
          <div className="grid md:grid-cols-4 gap-12 mb-16">
            <div>
              <h4 className="font-bold mb-6 text-yellow-500 tracking-tight">Research</h4>
              <ul className="space-y-3 text-sm text-white/40">
                <li>
                  <Link href="/methodology" className="hover:text-yellow-400 transition-colors duration-300 font-light">
                    Methodology
                  </Link>
                </li>
                <li>
                  <Link href="#" className="hover:text-yellow-400 transition-colors duration-300 font-light">
                    Publications
                  </Link>
                </li>
              </ul>
            </div>
            <div>
              <h4 className="font-bold mb-6 text-yellow-500 tracking-tight">Platform</h4>
              <ul className="space-y-3 text-sm text-white/40">
                <li>
                  <Link href="/upload" className="hover:text-yellow-400 transition-colors duration-300 font-light">
                    Upload Data
                  </Link>
                </li>
                <li>
                  <Link href="#" className="hover:text-yellow-400 transition-colors duration-300 font-light">
                    Documentation
                  </Link>
                </li>
              </ul>
            </div>
            <div>
              <h4 className="font-bold mb-6 text-yellow-500 tracking-tight">Resources</h4>
              <ul className="space-y-3 text-sm text-white/40">
                <li>
                  <Link href="#" className="hover:text-yellow-400 transition-colors duration-300 font-light">
                    API Docs
                  </Link>
                </li>
                <li>
                  <Link href="#" className="hover:text-yellow-400 transition-colors duration-300 font-light">
                    Tutorials
                  </Link>
                </li>
              </ul>
            </div>
            <div>
              <h4 className="font-bold mb-6 text-yellow-500 tracking-tight">Connect</h4>
              <ul className="space-y-3 text-sm text-white/40">
                <li>
                  <Link href="#" className="hover:text-yellow-400 transition-colors duration-300 font-light">
                    GitHub
                  </Link>
                </li>
                <li>
                  <Link href="#" className="hover:text-yellow-400 transition-colors duration-300 font-light">
                    Contact
                  </Link>
                </li>
              </ul>
            </div>
          </div>
          <div className="border-t border-white/5 pt-8 text-center text-xs text-white/20 uppercase tracking-widest font-medium">
            <p>&copy; 2025 BE Spectra. Advanced biological systems biology research.</p>
          </div>
        </div>
      </footer>
    </div>
  )
}
